
const Catalog = (props) => {
    return (
        <div>
            <a href="#">
                <label>{props.catalog.name}</label>
                <img src="../../static/images/26227.jpg"/>
                <div className="details">
                    <div className="catalogDetails"></div>
                    <div className="userDetails"></div>
                </div>
            </a>
        </div>
    )
}

export default Catalog;